














import './menu-bar.scss';
import { IRouterViewPath, viewPath, adminPath } from '../../core/interfaces/router-view-path';
import { IComponent } from '@/core/interfaces/component.interface';

export default {
  data() {
    return {
      viewPath,
    };
  },
  computed: {
    pathes() {
      const menuBar = this as unknown as IComponent;
      return menuBar.$store.getters.isAdmin ? adminPath : viewPath;
    }
  },
  methods: {
    fullPath(path: IRouterViewPath) {
      return path?.fullPath?.join('/') || path.path;
    },
  },
};
